<template>
    <div class="page">
        <div class="d-flex justify-content-between">
            <ul class="list-inline mt-2">
                <li class="list-inline-item mr-3">
                    <h5>Danh sách của tôi</h5>
                </li>
                <li class="list-inline-item mr-3">
                    <a 
                        href="#"
                        :class="getActiveClass(playlistsQuery.ordering, '-updated')"
                        @click.prevent="playlistsQuery.ordering = '-updated'">
                        Vừa cập nhật
                    </a>
                </li>
                <li class="list-inline-item mr-3">
                    <a 
                        href="#"
                        :class="getActiveClass(playlistsQuery.ordering, '-users_star')"
                        @click.prevent="playlistsQuery.ordering = '-users_star'">
                        Nhiều star
                    </a>
                </li>
                <li class="list-inline-item mr-3">
                    <a 
                        href="#"
                        :class="getActiveClass(playlistsQuery.ordering, '-views')"
                        @click.prevent="playlistsQuery.ordering = '-views'">
                        Xem nhiều
                    </a>
                </li>
            </ul>
            <b-form>
                <div class="input-group bg-white rounded-pill border mr-2 mb-2">
                    <div class="input-group-prepend">
                        <span class="input-group-text bg-transparent border-0">
                            <i class="ti ti-search"></i>
                        </span>
                    </div>
                    <input
                        type="text"
                        class="form-control border-0 pb-2 mr-4 pl-0"
                        placeholder="Tìm kiếm danh sách"
                        v-model.lazy="playlistsQuery.search"
                        @keyup="playlistsQuery.search = $event.target.value">
                </div>
            </b-form>
        </div>
        <PlayLists
            :query="playlistsQuery"
            :owner="isAuthenticated" />
    </div>
</template>
<script>
import { mapGetters } from "vuex"
import PlayLists from '@/components/PlayLists'

export default {
    name: 'MyPlayList',
    title: 'Danh sách của tôi',
    components: {
        PlayLists,
    },
    data() {
        return {
            playlistsQuery: {
                ordering: '-updated',
                search: '',
            },
        }
    },
    computed: {
        ...mapGetters(['isAuthenticated'])
    },
    methods: {
        getActiveClass(a, b) {
            return a == b ? 'color-active' : ''
        },
    }
}
</script>
